.footer-nav {
  color: $color-default-white;
  opacity: 0.7;
  display: flex;
  flex-direction: column;
  max-width: 470px;

  @include vp-767 {
    max-width: 100%;
  }
}

.footer-nav h2 {
  color: $color-default-white;
  padding-top: 5px;
  margin: 0 0 20px;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.05em;
  font-family: $ff-roboto;
  text-transform: uppercase;

  @include vp-1023 {
    padding-top: 0;
    margin-bottom: 22px;
  }

  @include vp-767 {
    display: none;
    padding: 20px 52px 20px 25px;
    margin-bottom: 0;
  }
}

.footer-nav button {
  display: none;
  color: $color-default-white;
  padding-top: 5px;
  margin: 0 0 20px;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.05em;
  font-family: $ff-roboto;
  text-transform: uppercase;
  background: $color-transparent;
  border: none;
  text-align: start;

  @include vp-1023 {
    padding-top: 0;
    margin-bottom: 22px;
  }

  @include vp-767 {
    display: block;
    padding: 20px 52px 20px 25px;
    margin-bottom: 0;
  }

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    opacity: 0.8;
    outline: none;
  }

  &:active {
    opacity: 0.5;
    outline: none;
  }
}

.footer-nav ul {
  display: grid;
  grid-template-columns: 220px 220px;
  grid-template-rows: repeat(4, auto);
  row-gap: 10px;
  column-gap: 30px;
  grid-auto-flow: column;

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    max-width: 220px;
    gap: 7.5px;
  }

  @include vp-767 {
    padding: 0 25px;
    max-width: 100%;
  }
}

.footer-nav a {
  font-family: $ff-ptmono;
  color: inherit;
  line-height: 22px;

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    opacity: 0.8;
    outline: none;
  }

  &:active {
    opacity: 0.5;
    outline: none;
  }
}
